.nav{
    position: fixed;
    top:0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width:100%;
    z-index: 2000;
    padding:12px 6% 8px;
    height: 95px;
}

.nav__sticky{
    background-color: #fff;
    box-shadow: rgba(60, 64, 67, 0.2) 0px 1px 2px 0px, rgba(60, 64, 67, 0.1) 0px 2px 6px 2px;
    .nav-logo{
        .logo-container{
            content: url('../../../assets/Navbar/blue-logo.png') !important;
        }
        h6{
            color:#111 !important;
        }
    }
    .nav-links{
        .inner-nav-link{
            color:#000 !important;
            svg{
                color:#444;
            }
        }
    }
    .nav-auth{
        .nav-login a{
            color:#000 !important;
        }
    }
}

.nav .nav-logo{
    display: flex;
    align-items: center;
    .logo-container{
        content: url('../../../assets/Navbar/white-logo.png');
        width: 45px;
        margin-right: 10px;
    }
    h6{
        color:white;
        font-size: 25px;
    }
}
.nav .nav-links{
    display: flex;
    .nav-link{
        margin:0 20px;
        padding:15px 0;
        position: relative;
        .megamenu-arrow{
            position: absolute;
            color:#ddd;
            right: 2px;
            top:47px;
            font-size: 25px;
            display: none;
        }
    }
    .inner-nav-link{
        color:white;
        font-size: 18px;
        display: flex;
        align-items: center;
        padding:4px 2.5px 6px;
        svg{
            font-size: 22.5px;
            margin:0 0 -2px 5px;
        }
    }
}
.nav .nav-auth{
    display: flex;
    align-items: center;
    a{
        font-size: 18px;
    }
    .nav-signup a{
        color:white;
        background-color: #427CFF;
        padding:12.5px 30px;
        border-radius: 22.5px;
        margin-left: 25px;
        &:hover {
            animation: pulse-white 0.8s ease-in-out;
        }
    }
    .nav-login a{
        color:white;
        &:hover{
            color:#427CFF !important;
        }
    }
}
.nav .megamenu{
    background-color: #fff;
    border:1px solid #eee;
    border-radius: 10px;
    position: absolute;
    padding:15px;
    display: flex;
    justify-content: space-around;
    top:62px;
    .inner-megamenu{
        width: 375px;
    }
    .megamenu-link{
        display: flex;
        align-items: center;
        width: 100%;
        margin:10px 0;
        height: 100px;
        padding:0 10px
    }
    .icon{
        display: flex;
        align-items: center;
        justify-content: center;
        border:1px solid #ccc;
        border-radius: 50%;
        width: 60px;
        height: 60px;
        font-size: 30px;
        margin-right: 15px;
        color:#777;
    }
    .block{
        flex:1;
        h6{
            font-size: 18px;
            color:#111;
            margin-bottom: 7.5px;
            display: flex;
            align-items: center;
            svg{
                display: none;
                font-size: 22.5px;
                color:#427CFF;
                margin-left: 5px;
            }
        }
        p{
            font-size: 16px;
            color:#777;
        }
    }
    h5{
        font-size: 19px;
        font-weight: 500;
        padding:0 10px;
    }
    .free2 h5{
        opacity:0;
    }
    .premium-icon h6 svg{
        display: block;
    }
}

.nav .features-link .megamenu{
    width: 1220px;
    left: -330px;
    display: none;
}
.nav .inner-megamenu2 h5{
    padding:0;
}
.nav .features-link:hover,
.nav .plans-link:hover,
.nav .business-link:hover{
    .arrow1{
        transform: rotate(-180deg);
        transition: 0.25s ease;
    }
    .megamenu{
        display: flex;
    }
    .megamenu-arrow{
        display: block;
    }
}

.nav .plans-link .megamenu{
    left: -350px;
    width: 1000px;
    display: none;
    .inner-megamenu2{
        width: fit-content;
    }
    .resources-links{
        margin-top: 15px;
        div{
            margin:20px 0;
        }
        div a{
            color:#111;
            font-weight: 600;
            font-size: 16px;
            &:hover{
                color:#427CFF;
            }
        }
    }
}

.nav .business-link .megamenu{
    width: 950px;
    left: -300px;
    display: none;
    .inner-megamenu{
        width: 430px;
    }
    .last-link a{
        display: flex;
        align-items: center;
        color:#427CFF;
        font-size: 17px;
        margin:20px 0 10px;
        svg{
            font-size: 22.5px;
            margin-right: 7.5px;
        }
    }
}

@media(max-width:1600px){
    .nav .nav-logo{
        h6{
            font-size: 22.5px;
        }
    }
    .nav .nav-links{
        .nav-link{
            margin:0 15px;
            .megamenu-arrow{
                right: 3px;
                font-size: 22.5px;
            }
        }
        .inner-nav-link{
            font-size: 17px;
            padding:5px 2px;
        }
    }
    .nav .nav-auth{
        a{
            font-size: 17px;
        }
        .nav-signup a{
            padding:10px 27.5px;
            border-radius: 20px;
            margin-left: 20px;
        }

    }
}
.nav .megamenu-link{
    transition: 0.2s ease;
}
.nav .megamenu-link:hover{
    .icon{
        border:1px solid #427CFF;
        background-color:#427CFF;
        color:#fff;
    }
    h6{
        color:#427CFF;
    }
}
@media(max-width:1420px){
    .nav__sticky{
        box-shadow: rgba(60, 64, 67, 0.2) 0px 0.5px 1px 0px, rgba(60, 64, 67, 0.1) 0px 1px 3px 1px;
    }
    .nav{
        padding:10px 5.5%;
        height: 75px;
    }
    
    .nav .nav-logo{
        .logo-container{
            width: 40px;
            margin-right: 7.5px;
        }
        h6{
            font-size: 20px;
        }
    }
    .nav .nav-links{
        .nav-link{
            margin:0 12.5px;
            padding:15px 0;
            .megamenu-arrow{
                right: 2px;
                top:45px;
                font-size: 20px;
            }
        }
        .inner-nav-link{
            font-size: 15px;
            padding:4px 1.5px 6px;
            svg{
                font-size: 17.5px;
            }
        }
    }
    .nav .nav-auth{
        a{
            font-size: 15px;
        }
        .nav-signup a{
            padding:10px 25px;
            border-radius: 20px;
            margin-left: 15px;
        }
    }
    .nav .megamenu{
        top:58px;
        .inner-megamenu{
            width: 300px;
        }
        .megamenu-link{
            padding:5px;
            margin:5px 0;
            height: 90px;
        }
        .icon{
            width: 50px;
            height: 50px;
            font-size: 27px;
            margin-right: 15px;
        }
        .block{
            h6{
                font-size: 15px;
                margin-bottom: 7.5px;
                svg{
                    font-size: 17.5px;
                }
            }
            p{
                font-size: 13px;
            }
        }
        h5{
            font-size: 16px;
        }
    }
    
    .nav .features-link .megamenu{
        width: 950px;
        left: -250px;
    }    
    .nav .plans-link .megamenu{
        left: -300px;
        width: 825px;;
        .resources-links{
            margin-top: 0px;
            div{
                margin:12.5px 0;
            }
            div a{
               font-size: 14px;
            }
        }
    }
    
    .nav .business-link .megamenu{
        width: 700px;
        left: -200px;
        display: none;
        .inner-megamenu{
            width: 330px;
        }
        .last-link a{
            font-size: 14px;
            margin:5px 0;
            svg{
                font-size: 20px;
                margin-right: 5px;
            }
        }
    }
}


@keyframes pulse-white {
    0% {
      box-shadow: 0 0 0 0 rgba(128, 173, 217, 0.4);
    }
  
    70% {
      box-shadow: 0 0 0 1.2rem rgba(128, 173, 217, 0);
    }
  
    100% {
      box-shadow: 0 0 0 0 rgba(128, 173, 217, 0);
    }
  }



.nav .nav-links{
    .inner-nav-link{
      
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        overflow: hidden;
    }
    .inner-nav-link:before {
        content: "";
        position: absolute;
        z-index: -1;
        left: 0;
        right: 100%;
        bottom: 0;
        background: #427CFF;
        height: 3px;
        -webkit-transition-property: right;
        transition-property: right;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }
    .inner-nav-link:hover:before, .inner-nav-link:focus:before, .inner-nav-link:active:before {
        right: 0;
    }
}

.mobile-nav{
    display: none;
}
@media(max-width:1200px){
    .mobile-nav{
        display: block;
    }
    .nav{
        height: fit-content;
        .nav-auth, .nav-links, .nav-logo{
            display: none;
        }
    }
}