footer{
    background-color: #323B43;
    color:white;
   
    width: 100%;
    padding:3% 5% 1%;
}
footer .row1{
    display: flex;
    justify-content: space-between;
}

footer .row1 .col2{
    width: 65%;
    display: flex;
    justify-content: space-between;
    .footer-list{
        width: fit-content;
        // border:1px solid red;
        margin:25px 10px;
    }
    .links{
        display: flex;
        flex-direction: column;
    }
    .links-col1{
        // border:1px solid white;
        width: fit-content;
        width: 25%;
    }
    .links-col2{
        width: 60%;
        flex:1;
        display: flex;
        justify-content: space-between;
    }
    h6{
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 10px;
    }
    a{
        color:#fff;
        margin:7.5px 0;
        font-size: 16px;
        &:hover{
            text-decoration: underline;
        }
    }
}


footer .row1 .col1{
    width: 27.5%;
    margin:25px 0;
//     border:1px solid #fff;
    .logo-row a{
        display: flex;
        align-items: center;
        h5{
            font-size: 22px;
            color:white;
        }
    }
    .logo-container{
        content: url('../../../assets/Navbar/white-logo.png');
        width: 40px;
        margin-right: 10px;
    }
    .p{
        font-size: 18px;
        margin:25px 0 35px;
    }
}

footer .col1 .email-container{
    margin-bottom: 35px;
    span{
        font-weight: 600;
        font-size: 15px;
    }
    .email{
        background-color: #555f68;
        display: flex;
        align-items: center;
        padding:5px;
        border-radius: 5px;
        margin:10px 0;
    }
    input{
        background: none;
        border:0;
        flex:1;
        height: 40px;
        padding:0 5px;
        color:#ccc;
        font-size: 16px;
        &::placeholder{
            color:#ccc;
        }
        &:focus{
            outline: none;
        }
    }
    button{
        background-color: #3C72ED;
        border-radius: 5px;
        color:#fff;
        border:0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: fit-content;
        height: 40px;
        font-size: 17px;
        padding:0 20px;
        cursor: pointer;
    }
}

footer .col1 .buttons{
    .span{
        font-weight: 600;
        font-size: 15px;
    }
    .inner-buttons{
        margin:10px 0;
        display: flex;
    }
    button{
        background-color: white;
        border:0;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        width: 160px;
        padding:10px 0;
        border-radius: 30px;
        &:first-child{
            margin-right: 20px;
        }
        svg{
            font-size: 20px;
            margin-right: 7.5px;
        }
        p{
            font-size: 14px;
        }
        h6{
            font-size: 13px;
        }
    }
}

footer .social-media-row{
    display: flex;
    justify-content: flex-end;
    a{
        font-size: 25px;
        color:#323B43;
        background-color: #555f68;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 45px;
        height: 45px;
        margin:0 5px;
        border-radius: 3px;
        &:hover{
            background-color: #7e878d;
            transition: 0.2s ease-in-out;
        }
    }

}

footer .copyright-row{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 5px;
    margin:40px 0 20px;
    border-top: 1px solid #aaa;
    .copyright{
        display: flex;
        align-items: center;
        flex: 1;
        margin-right: 25px;
    }
    p{
        display: flex;
        align-items: center;
        font-size: 16px;
        margin-right: 15px;
        svg{
            margin-right: 7.5px;
        }
    }
    a{
        font-size: 16px;
        color:#fff;
        margin:0 10px;
        &:hover{
            color:#aaa;
        }
    }
    

    .css-2b097c-container{
        width:155px !important;
        height: fit-content;
       .css-2b097c-container {
            color:white !important;
        }
        
    }
}

@media(max-width:1451px){
    footer .row1 .col2{
        width: 60%;
        .footer-list{
            margin:20px 7.5px;
        }
        h6{
            font-size: 15px;
            margin-bottom: 10px;
        }
        a{
            font-size: 13px;
        }
    }
      
    footer .row1 .col1{
        width: 30%;
        margin:15px 0;
        .logo-row a{
            h5{
                font-size: 18px;
            }
        }
        .logo-container{
            width: 35px;
        }
        .p{
            font-size: 15px;
            margin:20px 0 30px;
        }
    }
    
    footer .col1 .email-container{
        margin-bottom: 30px;
        span{
            font-size: 13px;
        }
        input{
            height: 35px;
            padding:0 5px;
            font-size: 13px;
        }
        button{
            height: 35px;
            font-size: 14px;
            padding:0 17.5px;
        }
    }
    
    footer .col1 .buttons{
        .span{
            font-size: 13px;
        }
        .inner-buttons{
            margin:10px 0;
        }
        button{
            width: 140px;
            padding:8px 0;
            border-radius: 25px;
            &:first-child{
                margin-right: 15px;
            }
            svg{
                font-size: 17px;
                margin-right: 7.5px;
            }
            p{
                font-size: 12px;
            }
            h6{
                font-size: 11px;
            }
        }
    }
    
    footer .social-media-row{
        a{
            font-size: 20px;
            width: 40px;
            height: 40px;
            margin:0 4px;
        }
    
    }
    
    footer .copyright-row{
        padding: 15px 4px;
        margin:30px 0 20px;
        .copyright{
            margin-right: 20px;
        }
        p{
            font-size: 13px;
            margin-right: 12.5px;
            svg{
                margin-right: 7.5px;
            }
        }
        a{
            font-size: 13px;
            margin:0 10px;
        }
            
    }
}

@media(max-width:1040px){
    footer .row1{
        display: block;
    }
    
    footer .row1 .col2{
        width: 100%;
    }
    footer .row1 .col1{
        width: 400px;
        max-width: 100%;
        margin:25px auto;
    }
    footer .copyright-row{
        flex-direction: column;
        .copyright{
            margin-bottom: 15px;
            p{
                text-align: center;
            }
        }
    }
}

@media(max-width:767px){
    footer{
        padding:30px 5%;
    }
    footer .row1 .col2{
        display: block;
        .footer-list{
            width: 47.5%;
            margin:15px 0px;
        }
        .links-col1{
            width: 100%;
            display: flex;
            justify-content: space-between;
        }
        .links-col2{
            width: 100%;
            flex:unset;
           flex-wrap: wrap;
        }
        h6{
            font-size: 14px;
        }
        a{
            margin:7.5px 0;
            font-size: 13px;
        }
    }
    
    
    footer .row1 .col1{
        .logo-row a{
            h5{
                font-size: 17px;
            }
        }
        .logo-container{
            width: 33px;
            margin-right: 7.5px;
        }
        .p{
            font-size: 14px;
            margin:15px 0 25px;
        }
    }
    
    footer .col1 .email-container{
        margin-bottom: 25px;
        span{
            font-size: 12px;
        }
        .email{
            margin:7.5px 0;
        }
        input{
            height: 32px;
            font-size: 13px;
        }
        button{
            height: 32px;
            padding:0 15px;
        }
    }
    
    footer .col1 .buttons{
        .span{
            font-size: 12px;
        }
        .inner-buttons{
            margin:10px 0;
            flex-wrap: wrap;
        }
        button{
            width: 130px;
            margin:5px 0;
            svg{
                font-size: 16px;
                margin-right: 7.5px;
            }
            p{
                font-size: 12px;
            }
            h6{
                font-size: 11px;
            }
        }
    }
    
    footer .social-media-row{
        justify-content: center;
        a{
            font-size: 18px;
            width: 37.5px;
            height: 37.5px;
        }
    
    }
    
    footer .copyright-row{
        flex-wrap: wrap;
        .copyright{
            justify-content: center;
            flex:unset;
            flex-wrap: wrap;
            width: fit-content;
            margin-right: 0px;
        }
        p{
            width: 100%;
            justify-content: center;
            font-size: 13.5px;
            margin:5px auto;
            svg{
                margin-right: 5px;
            }
        }
        a{
            font-size: 13.5px;
            margin:5px 10px;
        }
        .select-col{
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
        }
        .css-2b097c-container{
            width:155px !important;
            margin:30px auto 0;
        }
    
    }
}

@media(max-width:321px){
    footer .col1 .email-container{
        .email{
            padding:3px;
        }
        input{
            height: 32px;
            font-size: 11px;
        }
        button{
            height: 32px;
            padding:0 12px;
            font-size: 13px;
        }
    }
    
    footer .col1 .buttons{
        button{
            width: 120px;
            svg{
                font-size: 15px;
                margin-right: 5px;
            }
            p{
                font-size: 11px;
            }
            h6{
                font-size: 10px;
            }
        }
    }
}